jQuery('.gallery-item__link').on('click', function(e) {
    e.preventDefault()
    jQuery('body').addClass('lock');
    loadVideo(e.currentTarget.dataset.url)
})
console.log(5)
function loadVideo(id) {
    player.source = {
        type: 'video',
        sources: [
          {
            src: id,
            provider: 'youtube',
          },
        ],
      };
    jQuery('#modal').css({"display": "flex"});
}
